<template>
  <div class="home">
    <v-content>
      <v-container>
        <v-row align="center" class="mt-3" justify="center">
          <h1 class="display-5 mt-12 mx-auto red--text">
            Welcome to your digital notebook.
          </h1>
        </v-row>
        <v-row class="d-flex justify-center mt-12">
          <v-fade-transition mode="out-in">
            <h1 class="display-5" :key="index">{{ service }}</h1>
          </v-fade-transition>
        </v-row>
        <v-row>
          <v-col>
            <v-img
              height="400"
              width="500"
              class="mt-5 mx-auto img"
              src="../assets/sketching3.jpg"
              v-if="show"
            ></v-img>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      theme: {
        value: "dark"
      },
      isHome: true,
      timeInterval: null,
      services: [
        "Create â›·",
        "Edit ðŸ› ",
        "Save ðŸ”",
        "Turn into pdf ðŸŽ†",
        "All in one place â˜ðŸ¼"
      ],
      service: "",
      show: true,
      index: 1
    };
  },
  watch: {
    step(old, value) {
      this.enterClass = value > old ? "slide-in-top" : "slide-in-bottom";
    }
  },
  created() {
    this.updateService();
  },
  beforeDestroy() {
    window.clearInterval(this.timeInterval);
  },
  methods: {
    updateService() {
      this.service = this.services[0];
      this.timeInterval = window.setInterval(() => {
        this.service = this.services[this.index];
        this.index++;
        if (this.index === this.services.length) {
          this.index = 0;
        }
      }, 1500);
    }
  }
};
</script>
